<template>
  <div>
    <h3> 管理员列表</h3>
    <Divider orientation="left">列表筛选</Divider>
    <Row class="search">
      <Form inline :model="formData" :label-width="80">
        <FormItem label="编号">
          <Input v-model="formData.id" placeholder="请输入编号" class="filter-input"></Input>
        </FormItem>
        <FormItem label="真实姓名">
          <Input v-model="formData.real_name" placeholder="请输入名称" class="filter-input"></Input>
        </FormItem>

        <FormItem label="状态">
          <Select class="filter-input" v-model="formData.status" placeholder="请选择">
            <Option
              label="正常"
              :value="1">
            </Option>
            <Option
              label="禁用"
              :value="0">
            </Option>
          </Select>
        </FormItem>
        <FormItem label="运营团队">
          <Select v-model="formData.operation_team_id" class="filter-input" placeholder="请选择">
            <Option v-for="(item,index) in teams" v-bind:key="index" :label="item.name"
                    :value="item.id"></Option>
          </Select>
        </FormItem>
        <FormItem label="创建日期">
          <Date-picker v-model="formData.registerTime" class="filter-input" format="yyyy/MM/dd"
                       type="daterange"
                       placement="bottom-end" placeholder="选择日期"></Date-picker>
        </FormItem>
        <FormItem label="登录日期">
          <Date-picker v-model="formData.loginTime" class="filter-input" format="yyyy/MM/dd" type="daterange"
                       placement="bottom-end" placeholder="选择日期"></Date-picker>
        </FormItem>
        <FormItem label="登录IP">
          <Input v-model="formData.sort" placeholder="请输入排序" number class="filter-input"></Input>
        </FormItem>
        <FormItem label="创建IP">
          <Input v-model="formData.sort" placeholder="请输入排序" number class="filter-input"></Input>
        </FormItem>
      </Form>
      <div class="filter-btn">
        <span class="span">操作 </span>
        <Button style="font-weight: bold" type="info" icon="ios-search" @click="loadData(1)"> 搜索</Button>
        <Button style="font-weight: bold" type="warning" icon="md-refresh" @click="ref()">重置</Button>
        <Button style="font-weight: bold" type="success" icon="md-add" @click="add()">新建</Button>
      </div>
    </Row>
    <Divider orientation="left">列表数据</Divider>
    <Table stripe border :columns="columns" :data="listData.data"></Table>
    <Spin size="large" fix v-if="load"></Spin>
    <Row v-if="listData.last_page > 1" style="text-align: center;margin-top: 20px">
      <Page @on-change="loadData" :total="listData.total" :page-size="listData.per_page"
            :current="listData.current_page" size="small"/>
    </Row>
  </div>
</template>

<script>
import api from './api'
import teamApi from '../../operating/api'

export default {
  name: 'index',
  data () {
    return {
      formData: {},
      listData: {},
      teams: {},
      load: false,
      columns: [
        {
          title: 'id',
          align: 'center',
          key: 'id'
        },
        {
          title: '真实姓名',
          align: 'center',
          key: 'real_name'
        },
        {
          title: '创建日期',
          align: 'center',
          key: 'create_time'
        }, {
          title: '登录日期',
          align: 'center',
          key: 'login_time'
        },

        {
          title: '手机',
          align: 'center',
          key: 'mobile'
        },
        {
          title: '状态',
          key: 'status',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.status === 0 ? '禁用' : '正常')
          }
        },
        {
          title: '登录IP',
          key: 'login_ip',
          align: 'center'
        }, {
          title: '创建IP',
          key: 'create_ip',
          align: 'center'
        },
        {
          title: 'Action',
          width: 200,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'success',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.add(params.row.id)
                  }
                }
              }, '编辑'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.del(params.row)
                  }
                }
              }, '删除')
            ])
          }
        }

      ]
    }
  },
  activated () {
    this.loadData()
    this.loadTeam()
  },
  methods: {
    loadTeam () {
      teamApi.select((res) => {
        this.teams = res.data.data
      })
    },
    loadData (page) {
      if (page) {
        this.formData.jump_page = page
      }

      if (this.formData.registerTime != null && this.formData.registerTime.length === 2) {
        this.formData.register_start = this.formData.registerTime[0]
        this.formData.register_end = this.formData.registerTime[1]
      } else {
        this.formData.register_start = null
        this.formData.register_end = null
      }

      if (this.formData.loginTime != null && this.formData.loginTime.length === 2) {
        this.formData.login_start = this.formData.loginTime[0]
        this.formData.login_end = this.formData.loginTime[1]
      } else {
        this.formData.login_start = null
        this.formData.login_end = null
      }

      this.load = true
      api.adminList(this.formData, res => {
        this.listData = res.data.data
        this.load = false
      },
      () => {
        this.load = false
      })
    },
    add (id) {
      this.$router.push({
        name: 'adminEdit',
        query: {
          id: id
        }
      })
    },
    ref () {
      this.formData = {}
      this.loadData(1)
    },
    del (item) {
      this.$Modal.confirm({
        title: '确定删除' + item.name + '吗?',
        okText: '删除',
        cancelText: '取消',
        loading: true,
        onOk: () => {
          api.delete({ id: item.id }, () => {
            this.$Modal.remove()
            this.$Message.success('删除成功')
            this.loadData()
          },
          () => {
            this.$Modal.remove()
          })
        }
      })
    }
  }
}
</script>
